.block {
    border-radius: 8px;
    border: 1px solid var(--white);
    background: var(--bg-main);
    padding: 24px 16px;
    position: relative;
    border: 1px solid var(--dark-border);
}

.btns {
  display: flex;
  justify-content: space-between;
  border: 1px solid rgba(95, 122, 112, .4);
  border-left: none;
  border-right: none;
  width: calc(100% + 40px);
  position: relative;
  left: -20px;
  margin-bottom: 16px;
}

.btn {
  width: 50%;
  border-radius: 0;
  background: var(--primary);
  border: none;
  padding: 12px 16px;
  
  color: var(--placeholder);
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  transition: all ease .2s;
  cursor: pointer;
}

.activeBtn {
  background: #09202A;
  color: var(--light-blue);
}

.row {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  font-weight: 600;
  font-size: 10px;
  line-height: 16px;
  color: var(--light-blue);
}
.topText {
  color: var(--gray);
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  margin-bottom: 6px;
}

.bottomText {
  color: var(--light-blue);
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}

.row:not(:last-child) {
  margin-bottom: 16px;
}