.input {
    height: 53px;
    border-radius: 4px;
    width: 100%;
    border: 1px solid var(--gray-light);
    padding: 8px 16px;
    resize: none;
    
    color: var(--white);
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
}

.input::placeholder {
    color: var(--light);
}

.input:focus, .input:active {
    outline: none;
}