.checkbox {
    display: flex;
    align-items: center;
  }
  
  .label {
    display: flex;
    align-items: center;
    position: relative;
    font-size: 14px;
    line-height: 16px;
    color: var(--light-blue);
    cursor: pointer;
    box-sizing: border-box;
  }
  
  .inline .label,
  .outlined .label {
    justify-content: space-between;
    flex-flow: row-reverse;
    font-weight: 500;
    font-size: 16px;
    line-height: 56px;
    color: var(--light);
    -webkit-line-clamp: 1;
    text-overflow: ellipsis;
    background: var(--light-gray);
    border: 2px solid transparent;
    box-shadow: 0 0 15px rgba(4, 8, 16, 0.2);
    border-radius: 8px;
    padding: 0 20px;
    width: 100%;
    transition: all .2s;
  }
  
  .outlined .label:hover {
    opacity: .8;
  }
  
  .label::before {
    content: '';
    width: 15px;
    height: 15px;
    margin-right: 10px;
    border: 1px solid var(--light);
    background-position: center;
    border-radius: 2px;
    transition: all .2s;
  }
  
  .inline .label::before,
  .outlined .label::before {
    margin-right: 0;
    margin-left: 10px;
  }
  
  .inline .label {
    padding: 0;
    border: none !important;
  }
  
  .inline .label::before {
    margin-right: 0;
  }
  
  .input {
    display: none;
  }
  
  .inline .input {
    display: block;
    margin: 0;
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    cursor: pointer;
    opacity: 0;
  }
  
  .input:checked + .label::before {
    background: url(data:image/svg+xml;base64,) no-repeat center;
    border-color: var(--switcher-grey);
  }
  
  .radio .input:checked + .label::before {
    border-radius: 1px;
    background: var(--switcher-grey);
    border-color: var(--switcher-grey);
    box-shadow: inset 0 0 0 2px var(--primary);
  }
  
  .inline .input:checked + .label,
  .outlined .input:checked + .label {
    border-color: var(--light-blue);
  }
  
  .tooltip {
    width: 14px;
    height: 14px;
    margin-left: 15px;
    color: var(--light-blue);
  }
  
  .subtext {
    font-size: 12px;
    line-height: 14px;
    color: var(--gray);
    margin-top: 10px;
  }
  
  .agreement {
    margin-bottom: 30px;
  }
  
  .agreement .label {
    font-size: 14px;
    line-height: 20px;
    color: var(--gray);
  }
  
  .agreement .label::before {
    min-width: 15px;
    width: 15px;
    height: 15px;
    background: #282828;
    border-color: var(--switcher-grey);
    margin-right: 15px;
  }